.main {
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
}

/* Основные анимации для Header */
.floatSlow {
	animation: float-slow 12s ease-in-out infinite;
}

.floatDelayed {
	animation: float-delayed 15s ease-in-out infinite 3s;
}

.floatReverse {
	animation: float-reverse 18s ease-in-out infinite 2s;
}

.pulseSlow {
	animation: pulse-slow 8s ease-in-out infinite;
}

.pulseFast {
	animation: pulse-fast 4s ease-in-out infinite 1s;
}

.shimmerSlow {
	animation: shimmer-slow 12s ease-in-out infinite;
}

.glowPulse {
	animation: glow-pulse 6s ease-in-out infinite;
}

.orbit {
	animation: orbit 25s linear infinite;
}

.orbitReverse {
	animation: orbit-reverse 30s linear infinite 5s;
}

.twinkle {
	animation: twinkle 3s ease-in-out infinite;
}

.drift {
	animation: drift 20s ease-in-out infinite;
}

/* Сложные траектории движения */
@keyframes float-slow {
	0%,
	100% {
		transform: translate(0px, 0px) scale(1) rotate(0deg);
	}
	25% {
		transform: translate(-20px, -15px) scale(1.1) rotate(90deg);
	}
	50% {
		transform: translate(15px, -25px) scale(1.05) rotate(180deg);
	}
	75% {
		transform: translate(-10px, 10px) scale(1.15) rotate(270deg);
	}
}

@keyframes float-delayed {
	0%,
	100% {
		transform: translate(0px, 0px) scale(1) rotate(0deg);
	}
	33% {
		transform: translate(25px, -18px) scale(1.2) rotate(120deg);
	}
	66% {
		transform: translate(-18px, 22px) scale(1.1) rotate(240deg);
	}
}

@keyframes float-reverse {
	0%,
	100% {
		transform: translate(0px, 0px) scale(1) rotate(0deg);
	}
	25% {
		transform: translate(30px, 10px) scale(1.08) rotate(-90deg);
	}
	50% {
		transform: translate(-15px, -30px) scale(1.12) rotate(-180deg);
	}
	75% {
		transform: translate(20px, 15px) scale(1.06) rotate(-270deg);
	}
}

@keyframes pulse-slow {
	0%,
	100% {
		opacity: 0.1;
		transform: scale(1);
		filter: blur(40px);
	}
	50% {
		opacity: 0.2;
		transform: scale(1.3);
		filter: blur(50px);
	}
}

@keyframes pulse-fast {
	0%,
	100% {
		opacity: 0.05;
		transform: scale(1);
	}
	50% {
		opacity: 0.15;
		transform: scale(1.4);
	}
}

@keyframes shimmer-slow {
	0% {
		transform: translateX(-100%) rotate(45deg);
	}
	100% {
		transform: translateX(100%) rotate(45deg);
	}
}

@keyframes glow-pulse {
	0%,
	100% {
		box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
	}
	50% {
		box-shadow: 0 0 60px rgba(139, 92, 246, 0.5),
			0 0 100px rgba(59, 130, 246, 0.3);
	}
}

@keyframes orbit {
	0% {
		transform: rotate(0deg) translateX(100px) rotate(0deg);
	}
	100% {
		transform: rotate(360deg) translateX(100px) rotate(-360deg);
	}
}

@keyframes orbit-reverse {
	0% {
		transform: rotate(0deg) translateX(150px) rotate(0deg);
	}
	100% {
		transform: rotate(-360deg) translateX(150px) rotate(360deg);
	}
}

@keyframes twinkle {
	0%,
	100% {
		opacity: 0.3;
		transform: scale(1);
	}
	50% {
		opacity: 1;
		transform: scale(1.5);
	}
}

@keyframes drift {
	0%,
	100% {
		transform: translate(0, 0) rotate(0deg);
	}
	25% {
		transform: translate(30px, -20px) rotate(90deg);
	}
	50% {
		transform: translate(-20px, 40px) rotate(180deg);
	}
	75% {
		transform: translate(25px, 15px) rotate(270deg);
	}
}

/* Градиентные анимации */
.gradientFlow {
	background: linear-gradient(
		45deg,
		rgba(59, 130, 246, 0.1),
		rgba(139, 92, 246, 0.1),
		rgba(6, 182, 212, 0.1),
		rgba(236, 72, 153, 0.1)
	);
	background-size: 400% 400%;
	animation: gradientShift 8s ease infinite;
}

@keyframes gradientShift {
	0%,
	100% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
}

/* Эффекты частиц */
.particleField {
	background-image: radial-gradient(
			circle at 20% 80%,
			rgba(99, 102, 241, 0.3) 1px,
			transparent 1px
		),
		radial-gradient(
			circle at 40% 20%,
			rgba(139, 92, 246, 0.2) 2px,
			transparent 2px
		),
		radial-gradient(
			circle at 80% 40%,
			rgba(6, 182, 212, 0.25) 1px,
			transparent 1px
		),
		radial-gradient(
			circle at 60% 60%,
			rgba(236, 72, 153, 0.2) 3px,
			transparent 3px
		);
	background-size: 200px 200px, 150px 150px, 250px 250px, 180px 180px;
	animation: particleMove 30s linear infinite;
}

@keyframes particleMove {
	0% {
		background-position: 0 0, 0 0, 0 0, 0 0;
	}
	100% {
		background-position: 200px 200px, 150px 150px, 250px 250px, 180px 180px;
	}
}

/* Анимации для навбара */
.navbarGlow {
	animation: navbarGlow 4s ease-in-out infinite;
}

@keyframes navbarGlow {
	0%,
	100% {
		box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	}
	50% {
		box-shadow: 0 8px 40px rgba(59, 130, 246, 0.15);
	}
}

/* Адаптивность */
@media (max-width: 768px) {
	.floatSlow,
	.floatDelayed,
	.floatReverse {
		animation-duration: 20s;
	}

	.pulseSlow {
		animation-duration: 12s;
	}

	.orbit,
	.orbitReverse {
		animation-duration: 40s;
	}
}
